import React, { Component } from 'react'

export class About extends Component {
    render() {
        return (
            <React.Fragment>
            <div>
                <section className="templateux-hero overlay"  data-scrollax-parent="true">
                    {/* <div className="cover" data-scrollax="properties: { translateY: '30%' }"><img src="images/hero_2.jpg" alt="foto" /></div> */}

                    <div className="container">
                        <div className="row align-items-center justify-content-center intro">
                        <div className="col-md-10" data-aos="fade-up">
                            <h1>Nova Triansyah Azis</h1>
                            <p className="lead">Web Developer</p>
                            <a href="#next" className="go-down js-smoothscroll"></a>
                        </div>
                        </div>
                    </div>
                    </section>

                    <section className="templateux-section" id="next">
                    <div className="container py-5" data-aos="fade-up">
                        <div className="row">
                        <div className="col-md-12 clearfix mb-3">
                            <h2>Who am i?</h2>
                        </div>
                        <div className="col-md-6">
                            <p style={{textAlign: "justify"}}>Energetic, curious person. I
                            am a PHP developer with
                            demonstrated Web Development
                            experience. Able to work individually
                            or as a team</p>
                            <p style={{textAlign: "justify"}}>I have always been interested and curious in programming.</p>
                            
                        </div>

                        <div className="col-md-6">
                        <p style={{textAlign: "justify"}}>I am increasingly interested in the world
                                of information technology after I successfully completed a
                                program with PHP while at school. I always ready to explore myself in order to gain new fantastic experience, skills, and connection.</p>
                           </div>
                        </div>

         
                    {/* pengalaman */}
                    <div className="row templateux-section">
                    <div className="col-md-12 clearfix mb-5">
                        <h2>Work Experience</h2>
                    </div>
                    <div className="col-md-6 col-lg-4 mb-4">
                        <div className="block-38 ">
                        <div className="block-38-img">
                            <div className="block-38-header">
                            <img src="images/mitreka.png" alt="Image placeholder" />
                            <h3 className="block-38-heading">PT Mitreka Solusi Indonesia</h3>
                            <p className="block-38-subheading">2019 - Now</p>
                            </div>
                            <div className="block-38-body">
                            <p>Making applications according to analyst design and system requirements. <br />
                            Ensuring that the application matches the user's needs. <br />
                            Responsible for making application program modules according to the
                            direction of the Team Leader / System Analyst and the results of the analysis
                            and design that has been made.<br /> <br />
                            Perform repairs and bug fixing (if found) during the software maintenance
                            process.</p>
                            </div>
                        </div>
                        </div>
                    </div>
                    </div>

                    {/* skills */}
                    <div className="row templateux-section">
                    <div className="col-md-12 clearfix mb-5">
                        <h2>Top Skills</h2>
                    </div>
                    <div className="col-md-6 col-lg-4 mb-4">
                        <div className="block-38 ">
                        <div className="block-38-img">
                            <div className="block-38-header">
                            <h3 className="block-38-heading">Web Development</h3>
                            <h3 className="block-38-heading">PHP</h3>
                            <h3 className="block-38-heading">React JS</h3>
                            </div>
                            <div className="block-38-body">
                            </div>
                        </div>
                        </div>
                    </div>
                    <div className="col-md-6 col-lg-4 mb-4">
                        <div className="block-38 ">
                        <div className="block-38-img">
                            <div className="block-38-header">
                            <h3 className="block-38-heading">Codeigniter</h3>
                            <h3 className="block-38-heading">Jquery</h3>
                            </div>
                            <div className="block-38-body">
                            </div>
                        </div>
                        </div>
                    </div>
                    <div className="col-md-6 col-lg-4 mb-4">
                        <div className="block-38 ">
                        <div className="block-38-img">
                            <div className="block-38-header">
                            <h3 className="block-38-heading">HTML</h3>
                            <h3 className="block-38-heading">Bootstrap</h3>
                            </div>
                            <div className="block-38-body">
                            </div>
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
                

                <div className="container-fluid">
                    <div className="row">
                    <div className="col-md-12" data-aos="fade-up">
                        <div className="owl-carousel dots-overlap wide-slider">
                        <div className="item"><img src="images/codingan.jpg" alt="Free Template" className="img-fluid" /></div>
                        <div className="item"><img src="images/ngoding.jpg" alt="Free Template" className="img-fluid" /></div>
                        <div className="item"><img src="images/hero_3.jpg" alt="Free Template" className="img-fluid" /></div>
                        </div>
                    </div>
                    </div>
                    </div>
                </section>
            </div>
        </React.Fragment>
        )
    }
}

export default About;
