import React, { Component } from 'react'
import { Link } from 'react-router-dom'

class Contact extends Component {
    render() {
        return (
            <React.Fragment>
            <section className="templateux-hero mb-5"  data-scrollax-parent="true">
      {/* <!-- <div className="cover" data-scrollax="properties: { translateY: '30%' }"><img src="images/hero_2.jpg" /></div> --> */}

      <div className="container">
        <div className="row align-items-center justify-content-center intro">
          <div className="col-md-10" data-aos="fade-up">
            <h1>Get in touch</h1>
            <p className="lead">Contact Me Through:</p>
            <a href="#next" className="go-down js-smoothscroll"></a>
          </div>
        </div>
      </div>
    </section>
    
    <section className="templateux-portfolio-overlap mb-5" data-aos="fade-up" id="next">
      <div className="container">
            <div className="row">
              <div className="col-md-4  mb-4">
                  <Link to="LinkedIn" onClick={() => window.location.href = 'https://www.linkedin.com/in/nova-azis-823185178/'} className="form-control">LinkedIn</Link>
              </div>
              <div className="col-md-4  mb-4">
                  <Link to="email" onClick={() => window.location.href = 'mailto:novaajis20@gmail.com'} className="form-control">Email</Link>
              </div>
              <div className="col-md-4  mb-4">
                  <Link to="cv" onClick={() => window.location.href = 'https://drive.google.com/file/d/1JT2RdQ6NKpcZBSLV550MUIb2wXLl1BGJ/view?usp=sharing'} className="form-control">CV</Link>
              </div>
              <div className="col-md-4  mb-4">
                  <label className="form-control">Phone Number / WhatsApp</label>
              </div>
              <div className="col-md-4  mb-4">
                  <label className="form-control">+628979825135</label>
              </div>
            </div>
      </div>
    </section>

    </React.Fragment>
        )
    }
}

export default Contact;
